:root {
  font: 17px sans-serif;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0 12.5%;
}

main {
  background: radial-gradient(#ddd, #fff);
  border-top: none;
  border-radius: 0 0 18px 18px;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
  display: flex;
}

main > h1 {
  text-shadow: -4px 4px #aaf8;
  font-size: 2.8rem;
}

#control-container {
  width: 369px;
}

#query-mode {
  color: #ccc;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.2rem;
  font-weight: bold;
  display: flex;
}

#query-mode > * {
  width: 33.3333%;
}

#query-mode > div:first-child {
  text-align: right;
  padding-right: 1rem;
}

#query-mode > div:last-child {
  text-align: left;
  padding-left: 1rem;
}

#query-switch {
  width: 6rem;
  height: 2.1rem;
  cursor: pointer;
  background-color: #dfdfe1;
  border: 3px solid #ccc;
  border-radius: 2rem;
  align-items: center;
  transition: all .2s;
  display: flex;
}

#query-switch:hover {
  border-color: #999;
}

#query-switch > div {
  height: 1.8rem;
  width: 1.8rem;
  background-color: #77a;
  border-radius: 50%;
  margin: 0 .2rem;
  transition: all .2s;
}

#query-switch:hover > div {
  background-color: #55b;
}

#controls {
  color: #aaa;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

#controls input {
  box-sizing: border-box;
  width: 100%;
  color: #aaa;
  border: 2px solid #ccc;
  border-radius: 4px;
  margin-top: 5px;
  padding: 5px;
  font-size: .8rem;
  display: block;
}

#coordinate-submit {
  color: #999;
  width: 100%;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #dfdfe1;
  border: 3px solid #ccc;
  border-radius: 12px;
  margin-top: 1.8rem;
  padding: 1rem;
  font: bold 1rem sans-serif;
  display: block;
}

#coordinate-submit:hover {
  box-shadow: inset 0 0 8px #8888;
}

#coordinate-submit:active {
  box-shadow: inset 0 0 8px #66c8;
}

#query-content {
  text-align: center;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1.1rem;
  padding: 1.1rem;
  display: flex;
}

#city {
  text-transform: uppercase;
  font: bold 1em / 1.5em sans-serif;
}

#city-info {
  text-transform: uppercase;
  color: #0008;
  margin-bottom: 1.4rem;
  font: 1em / 1.5em sans-serif;
}

#forecast {
  font: 2.4rem sans-serif;
}

#temperature {
  font: bold 4.3rem sans-serif;
}

@media (max-width: 530px) {
  :root {
    font-size: 14px;
  }

  #control-container {
    width: 240px;
  }

  #controls {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 420px) {
  :root {
    font-size: 13px;
  }
}

@media (max-width: 380px) {
  :root {
    font-size: 11px;
  }

  #control-container {
    width: 200px;
  }
}

